html {
  box-sizing: border-box;
  font-size: 100%; /* 16px */
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

body {
  font-family: "Sora";
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "Sora";
  font-weight: 700;
}

h1 {
  font-size: 38px;
}

h2 {
  font-size: 32px;
}

h3 {
  font-size: 24px;
}

.title {
  margin: 30px;
}

.button {
  background: #a1f25f;
  border: none;
  border-radius: 5px;
  padding: 10px;
  transition: all 0.3s;
  cursor: pointer;
}
.button:hover {
  background: #8bef39;
  box-shadow: 0px 0px 24px 0 #8bef39;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2px;
  background: #fcfbf8;
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.25);
  flex-direction: column;
}
@media (min-width: 600px) {
  .header {
    flex-direction: row;
  }
}
.header .header__shopping-basket {
  margin: 0;
  order: 1;
}
@media (min-width: 600px) {
  .header .header__shopping-basket {
    order: inherit;
    margin-right: 40px;
  }
}

/* MENU */
@keyframes anim-bar-top {
  0% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  50% {
    transform: translate3d(0, 10px, 0) rotate(0deg);
  }
  100% {
    transform: translate3d(0, 10px, 0) rotate(45deg);
  }
}
@keyframes anim-bar-top-rev {
  0% {
    transform: translate3d(0, 10px, 0) rotate(45deg);
  }
  50% {
    transform: translate3d(0, 10px, 0) rotate(0deg);
  }
  100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
}
@keyframes anim-bar-middle {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(0);
    opacity: 0;
  }
}
@keyframes anim-bar-middle-rev {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes anim-bar-bottom {
  0% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  50% {
    transform: translate3d(0, -10px, 0) rotate(0deg);
  }
  100% {
    transform: translate3d(0, -10px, 0) rotate(135deg);
  }
}
@keyframes anim-bar-bottom-rev {
  0% {
    transform: translate3d(0, -10px, 0) rotate(135deg);
  }
  50% {
    transform: translate3d(0, -10px, 0) rotate(0deg);
  }
  100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
}
.menu {
  order: 2;
  margin: 30px 0;
}
@media (min-width: 600px) {
  .menu {
    order: inherit;
    margin: 0 0 0 40px;
  }
}

.drop-menu {
  display: flex;
  background: #a1f25f;
  width: 100%;
  height: auto;
  padding: 20px;
  z-index: 5;
  position: absolute;
  top: 212px;
  left: 0;
  opacity: 1;
  transition: all 0.4s ease-in-out;
  /* Links hover animation */
}
@media (min-width: 600px) {
  .drop-menu {
    top: 90px;
  }
}
.drop-menu .drop-menu__link {
  position: relative;
  color: #1f1b0f;
  margin-right: 50px;
}
.drop-menu .drop-menu__link:first-of-type {
  margin-left: 20px;
}
.drop-menu .drop-menu__link:last-of-type {
  margin: 0;
}
.drop-menu .drop-menu__link {
  transition: all 0.3s;
}
.drop-menu .drop-menu__link::before {
  content: "";
  display: block;
  background: #fcfbf8;
  height: 3px;
  width: 0;
  position: absolute;
  bottom: -1px;
  overflow: hidden;
  transition: all 0.3s;
}
.drop-menu .drop-menu__link:hover {
  transform: scale(1.05);
}
.drop-menu .drop-menu__link:hover::before {
  width: 100%;
  overflow: hidden;
}
.drop-menu.hidden {
  visibility: hidden;
  background: #fcfbf8;
  opacity: 0;
  width: 35%;
}

/* ANIMATIONS */
.bar-top,
.bar-middle,
.bar-bottom {
  background: #000;
  height: 4px;
  width: 40px;
  border-radius: 3px;
  margin-bottom: 6px;
}
.bar-top:last-of-type,
.bar-middle:last-of-type,
.bar-bottom:last-of-type {
  margin-bottom: 0;
}
.bar-top.not-active,
.bar-middle.not-active,
.bar-bottom.not-active {
  animation: none;
}

.bar-top {
  animation: anim-bar-top-rev 0.7s ease-out;
}

.bar-middle {
  animation: anim-bar-middle-rev 0.7s ease-out;
}

.bar-bottom {
  animation: anim-bar-bottom-rev 0.7s ease-out;
}

.active.bar-top {
  animation: anim-bar-top 0.7s ease forwards;
}
.active.bar-middle {
  animation: anim-bar-middle 0.7s ease forwards;
}
.active.bar-bottom {
  animation: anim-bar-bottom 0.7s ease forwards;
}

.ads-container {
  position: relative;
  height: 300px;
  z-index: 0;
}

.slide-image {
  width: 100%;
  height: 300px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0;
  position: absolute;
  transition: all 0.4s;
}
.slide-image.active {
  opacity: 1;
}

.popular-products__title {
  margin: 30px;
}

.products-slider-container {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 20px 40px 20px;
}
@media (min-width: 600px) {
  .products-slider-container {
    margin: 0 0 40px 0;
  }
}

.arrow {
  background: none;
  border: none;
  cursor: pointer;
  display: none;
}
@media (min-width: 600px) {
  .arrow {
    display: block;
  }
}
.arrow#arrow-left {
  visibility: hidden;
}

.about__title {
  margin: 30px;
}

.about__content {
  position: relative;
  background: #8bef39;
  padding: 40px 0;
  display: grid;
  place-items: center;
}
.about__content .about__paragraph {
  margin: 0;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  width: 60%;
  color: #413a2b;
}

.info {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0;
  place-content: center;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 20px;
  padding: 40px 0 5px 0;
  background: #504835;
  color: #fcfbf8;
  box-shadow: 0px 0px 30px 3px rgba(252, 251, 248, 0.5);
}
@media (min-width: 600px) {
  .info {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
}
@media (min-width: 800px) {
  .info {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
  }
}

.info__content {
  display: grid;
  place-items: center;
  text-align: center;
}
@media (min-width: 600px) {
  .info__content:first-of-type {
    margin-left: 20px;
  }
}
.info__content h2 {
  font-size: 30px;
  min-height: 4em;
  vertical-align: text-bottom;
  margin: 15px 0 0 0;
}
.info__content svg {
  width: 100px;
  height: 100px;
  stroke: #a1f25f;
}

.footer {
  background: #7e7153;
  height: 200px;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: relative;
}
.footer .footer__logo {
  position: absolute;
  left: 15px;
  top: 15px;
}
.footer p {
  text-align: center;
  margin: 0 0 10px 0;
}

.products-container {
  display: grid;
  place-content: center;
}

.products {
  display: grid;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 10px;
}
@media (min-width: 600px) {
  .products {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }
}
@media (min-width: 800px) {
  .products {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }
}
@media (min-width: 1200px) {
  .products {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
  }
}

.popular-products-slider {
  display: flex;
  align-items: center;
  gap: 30px;
  overflow: auto;
  scrollbar-width: none;
  scroll-behavior: smooth;
  position: relative;
}

.product-container {
  display: grid;
  place-content: center;
  text-align: left;
  padding: 25px;
  border-radius: 26px;
  background: #6f6449;
  transition: all 0.3s;
  scale: 0.95;
}
.product-container:hover {
  scale: 1;
}
.product-container .product-name,
.product-container .product-price {
  font-weight: bold;
  color: #fcfbf8;
}
.product-container .product-price {
  color: #a1f25f;
  margin: 0;
}
.product-container .product-name {
  margin: 24px 0 10px 0;
}
.product-container .product-image {
  width: 175px;
  height: 175px;
  object-fit: cover;
  border-radius: 6px;
}
@media (min-width: 600px) {
  .product-container .product-image {
    width: 200px;
    height: 200px;
  }
}

.search {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 600px) {
  .search {
    flex-direction: row;
  }
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 0 0 30px 0;
}
@media (min-width: 600px) {
  .pagination {
    margin: 0 30px 0 0;
    flex-direction: row;
  }
}
.pagination .pagination__button {
  background-color: #a1f25f;
  border: none;
  padding: 10px;
  border-radius: 50%;
  width: 40px;
  height: 40px;
}

.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  padding: 40px;
  gap: 20px;
  position: relative;
  margin-top: 40px;
}
@media (min-width: 800px) {
  .product {
    margin: 0;
    flex-direction: row;
    align-items: flex-start;
  }
}
.product .product__image {
  margin-top: 30px;
  width: 100%;
}
@media (min-width: 600px) {
  .product .product__image {
    margin: 0;
  }
}
.product .product__image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.product__info {
  width: 100%;
  display: flex;
  flex-direction: column;
}
@media (min-width: 800px) {
  .product__info {
    align-items: flex-start;
  }
}
.product__info .product__description,
.product__info .product__price {
  margin: 10px 0 0 0;
}
.product__info .product__price {
  margin: 10px 0 20px 0;
  font-weight: bold;
}
.product__info .product__name {
  margin: 0;
  font-size: 28px;
  position: absolute;
  top: 0;
}
@media (min-width: 800px) {
  .product__info .product__name {
    position: relative;
  }
}
.product__info .add {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}